@import url(https://fonts.googleapis.com/css2?family=Tiro+Gurmukhi:ital@1&display=swap);
*{
  margin: 0;
  font-family: "TiroGurmukhi";
  color: white;
}
.about-me{
    -webkit-animation: fadeIn 7s;
            animation: fadeIn 7s;
    margin-top: 32px;
    line-height: 2;
    border-style: ridge;
    border-color: white;
    border-radius: 8px;
    width: 600px;
    padding: 20px;
  }
  .about-me p{
    font-size: 20px;
  }
  .about-me h2{
    -webkit-text-decoration: inset;
            text-decoration: inset;
  }
  .quote{
    -webkit-animation: fadeIn 7s;
            animation: fadeIn 7s;
    font-size: 20px;
    margin-top: 100px;
    line-height: 1.5;
    border-style: ridge;
    border-color: white;
    border-radius: 8px;
    width: 600px;
    text-align: right;
    padding: 20px;
    float: right;
  }
  .homepage{
    margin: 20px;
  }
  @-webkit-keyframes fadeIn {
    from{
      opacity: 0;
    }
    to{
      opacity: 1;
    }
  }
  @keyframes fadeIn {
    from{
      opacity: 0;
    }
    to{
      opacity: 1;
    }
  }
.App {
  background-color: #242424;
  background-size: cover;
  position: fixed;
  min-width: 100%;
  min-height: 100%;
  background-position: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

