.heading{
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: px;
  margin: 0 auto;
  justify-content: space-between;
}
.heading h1{
  -webkit-animation: fadeIn 4s;
          animation: fadeIn 4s;
  text-decoration: underline;
  border-radius: 8px;
}
.heading a{
  -webkit-animation: fadeIn 4s;
          animation: fadeIn 4s;
  color:black;
  background-color: cyan;
  border-radius: 8px;
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navigation a:hover{
  color: white;
}
@-webkit-keyframes fadeIn {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}
@keyframes fadeIn {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

.myphoto{
    margin-right: 30px;
    width: 50px;
    height: 50px;
    border-radius: 100%;
}
.socialimg{
    width: 25px;
    height: 25px;
    margin-right: 10px;
}
.socialtag{
    margin-right: 20px;
    display: flex;
    align-items: center;
}
.footer{
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 30px;
    left: 30px;
}
.socialLink{
    text-decoration: none;
    color: white;
}
.cardholder{
    -webkit-animation: fadeIn 5s;
            animation: fadeIn 5s;
    display: flex;
    justify-content: space-around;
}
.projects{
    -webkit-animation: fadeIn 3s;
            animation: fadeIn 3s;
   margin: 20px; 
}
.cardimg{
    border-radius: 10px 0px 0px 10px;
    height: 100%;
    margin-bottom: 20px;
    justify-content: center;
    width: 300px;
}
.card{
    border-radius: 10px;
    display: flex;
    margin: 20px;
    flex: 1 1;
    background-color: orange;
}
.cardtext{
    padding: 20px;
}
.clink{
    background-color: crimson;
    border-radius: 8px;
    margin-left: 16px;
    text-decoration: none;
    padding: 6px 10px;
}
.cardlinks{
    display: flex;
}
.clink a:hover{
    color: purple;
}
@-webkit-keyframes fadeIn {
    from{
